import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
import { TranslateService } from '@ngx-translate/core';

/**
 * Generated class for the GratisPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-gratis',
  templateUrl: 'gratis.html',
})
export class GratisPage {
  nuevas: any;

  constructor(private translateService: TranslateService,private http: HttpClient, public navCtrl: NavController, public navParams: NavParams) {
    http.get('https://www.freefire.wiki/api' + '/follows.json', {})
        .toPromise()
          .then((response:any) => {
            this.nuevas = response.FOLLOW;
            console.log(response);
            
          })
    
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad GratisPage');
  }

  link(link:string){
    window.open(
      link,
      "_blank"
    );

  }

  idioma(id:string){
//Language
this.translateService.setDefaultLang(id);
this.translateService.use(id);
  }

}



